import {
  Body,
  Controller,
  Get,
  GoneException,
  Headers,
  HttpCode,
  Logger,
  Post,
  Req,
  Request,
  UseGuards,
  Version,
  VERSION_NEUTRAL,
} from '@nestjs/common'
import {
  CustomerAuthService,
  customerPreSignupDto,
  CustomerTx,
  CustomerUpdateAmountDto,
} from '@flowda-projects/v1-flowda-services'
import { AppJwtAuthGuard } from '../app/appJwtAuth.guard'
import { CustomerJwtAuthGuard } from './customerJwtAuth.guard'
import { CustomerWeiXinAuthGuard } from './customerWeiXinAuth.guard'
import { FwhLoginSimpleGuard } from './fwhLoginSimple.guard'
import { CustomerAppCombinedAuthGuard } from './customerAppCombinedAuth.guard'
import {
  amountUpdateSchemaDto,
  customerPreSignupSchemaDto,
  customerSignupSchemaDto,
  generateRecoveryCodeSchemaDto,
  ResetPasswordDto,
  resetPasswordWithRecoveryCodeSchemaDto,
} from '@flowda-projects/flowda-shared-types'

@Controller('sdk/customer')
export class CustomerController {
  private readonly logger = new Logger('CustomerController')

  constructor(private customerAuth: CustomerAuthService, private customerTx: CustomerTx) {}

  /**
   * 注册用户，必须确保 app 已经 verify (SdkAppJwtAuthGuard)
   * 验证方法是 bearer token 带 app 相关的 access token
   */
  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Post('preSignup')
  preSignup(@Req() req, @Body() dto: Omit<customerPreSignupDto, 'appId'>) {
    this.logger.log('pre sign up: ', dto)
    return this.customerAuth.preSignup({
      email: dto.email,
      appId: req.user.id,
    })
  }

  @Version('4')
  @Post('preSignup')
  preSignupV4(@Req() req, @Body() dto: customerPreSignupSchemaDto) {
    throw new GoneException()
  }

  // todo 没看到 signup v1 但是 customerAuth.verifyAndSignup 逻辑再，应该是误删除了 暂时不恢复
  @Version('4')
  @Post('signup')
  signupV4(@Req() req, @Body() dto: customerSignupSchemaDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard, CustomerAppCombinedAuthGuard)
  @Post('login')
  @HttpCode(200)
  login(@Request() req) {
    return req.user
  }

  @Version('4')
  @Post('login')
  async loginV4(@Req() req) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @Post('refreshToken')
  @HttpCode(200)
  refreshToken(@Headers('Refresh') rt: string) {
    return this.customerAuth.refreshToken(rt)
  }

  @Version('4')
  @Post('refreshToken')
  @HttpCode(200)
  refreshTokenV4(@Req() req, @Headers('Refresh') rt: string) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Post('logout')
  @HttpCode(200)
  async logout(@Request() req) {
    await this.customerAuth.logoutApi(req.user.id)
    return {
      success: true,
    }
  }

  @Version('4')
  @Post('logout')
  @HttpCode(200)
  async logoutV4(@Request() req) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Post('generateRecoveryCode')
  @HttpCode(200)
  async generateRecoveryCode(@Request() req, @Body() dto: generateRecoveryCodeSchemaDto) {
    const appId = req.user.id
    this.logger.debug('get appId from request after guard: ', appId)
    return this.customerAuth.generateRecoveryCode({
      ...dto,
      appId,
    })
  }

  @Version('4')
  @Post('generateRecoveryCode')
  @HttpCode(200)
  async generateRecoveryCodeV4(@Request() req, @Body() dto: generateRecoveryCodeSchemaDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Post('resetPassword')
  @HttpCode(200)
  async resetPassword(@Request() req, @Body() dto: Omit<ResetPasswordDto, 'appId'>) {
    const appId = req.user.id
    this.logger.debug('reset pw and get appId from request after guard: ', appId)
    await this.customerAuth.resetPassword({
      ...dto,
      appId,
    })
    return {
      success: true,
    }
  }

  @Version('4')
  @Post('resetPassword')
  @HttpCode(200)
  async resetPasswordV4(@Request() req, @Body() dto: resetPasswordWithRecoveryCodeSchemaDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Get()
  async query(@Req() req) {
    return req.user
  }

  @Version('4')
  @Get()
  async queryV4(@Req() req) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Post('amount')
  async updateAmount(@Req() req, @Body() dto: CustomerUpdateAmountDto) {
    try {
      const updated = await this.customerTx.amountUpdate({
        userInfo: req.user,
        action: dto.action,
        count: dto.count,
      })
      return updated
    } catch (error) {
      console.error('user amount update failed: ', error)
      return {
        success: false,
        message: error?.message,
      }
    }
  }

  @Version('4')
  @Post('amount')
  async updateAmountV4(@Req() req, @Body() dto: amountUpdateSchemaDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard, CustomerWeiXinAuthGuard)
  @Post('weixin/login')
  async getWXAccessToken(@Req() req) {
    return req.user
  }

  @Version('4')
  @Post('weixin/login')
  async getWXAccessTokenV4(@Req() req) {
    throw new GoneException()
  }

  @Version(VERSION_NEUTRAL)
  @UseGuards(AppJwtAuthGuard, FwhLoginSimpleGuard)
  @Post('weixin/fwhLoginMerge')
  async getFwhAccessToken(@Req() req, @Body() dto) {
    const appId = req.user.appId
    this.logger.debug('fwh login merge with appId: ', appId)
    return this.customerAuth.fwhLoginMerge(dto.code, appId)
  }

  /*
  https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx16aa373d85f92806&redirect_uri=https%3A%2F%2Fpay.freecharger.cn%2Fwx-h5-login-debug&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect
   */
  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard, FwhLoginSimpleGuard)
  @Post('weixin/fwhLogin')
  async fwhLogin(@Req() req, @Body() dto) {
    const appId = req.user.appId
    this.logger.debug('fwh login with appId: ', appId)
    return this.customerAuth.fwhLogin(dto.code, appId)
  }

  /*
  https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx16aa373d85f92806&redirect_uri=https%3A%2F%2Fpay.freecharger.cn%2Fwx-h5-login-debug&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect
   */
  @Version('4')
  @Post('weixin/fwhLogin')
  async fwhLoginV4(@Req() req, @Body() dto) {
    throw new GoneException()
  }
}
