import {
  Body,
  Controller,
  GoneException,
  Headers,
  HttpCode,
  Post,
  Req,
  UseGuards,
  Version,
  VERSION_NEUTRAL,
} from '@nestjs/common'
import { AppAuthService } from '@flowda-projects/v1-flowda-services'
import { AppLocalAuthGuard } from './appLocalAuth.guard'

@Controller({
  path: 'sdk/app',
})
export class AppController {
  constructor(private appAuth: AppAuthService) {}

  @Version([VERSION_NEUTRAL, '1'])
  @Post('create')
  async create(@Body() dto) {
    return this.appAuth.create(dto)
  }

  @Version('4')
  @Post('create')
  async createV4(@Body() dto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppLocalAuthGuard)
  @Post('verify')
  @HttpCode(200)
  verify(@Req() req: any) {
    // 返回 at rt，客户端负责存储策略
    return req.user
  }

  @Version('4')
  @Post('verify')
  @HttpCode(200)
  verifyV4(@Req() req: any) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @Post('refreshToken')
  @HttpCode(200)
  refreshToken(@Headers('Refresh') rt: string) {
    return this.appAuth.appRefreshToken(rt)
  }

  @Version('4')
  @Post('refreshToken')
  @HttpCode(200)
  refreshTokenV4(@Headers('Refresh') rt: string) {
    throw new GoneException()
  }
}
