import { Module, ValidationPipe } from '@nestjs/common'
import { ServicesModule } from '../services/services.module'
import { AppExceptionFilter } from '@flowda-projects/v1-flowda-services'
import { APP_FILTER, APP_PIPE } from '@nestjs/core'
import { CustomerController } from '../customer/customer.controller'
import { AppController } from '../app/app.controller'
import { AppLocalAuthStrategy } from '../app/appLocal.strategy'
import { AppJwtStrategy } from '../app/appJwt.strategy'
import { CustomerLocalStrategy } from '../customer/customerLocal.strategy'
import { CustomerJwtStrategy } from '../customer/customerJwt.strategy'
import { OrderController } from '../order/order.controller'
import { ProductController } from '../product/product.controller'
import { CustomerWeiXinStrategy } from '../customer/customerWeiXin.strategy'
import { FwhLoginSimpleStrategy } from '../customer/fwhLoginSimple.strategy'
import { CustomerAppCombinedStrategy } from '../customer/customerAppCombined.strategy'

export const sdkModuleControllers = [AppController, CustomerController, OrderController, ProductController]
export const sdkModuleProviders = [
  {
    provide: APP_FILTER,
    useClass: AppExceptionFilter,
  },
  {
    provide: APP_PIPE,
    useClass: ValidationPipe,
  },
  AppLocalAuthStrategy,
  AppJwtStrategy,
  CustomerLocalStrategy,
  CustomerJwtStrategy,
  CustomerWeiXinStrategy,
  FwhLoginSimpleStrategy,
  CustomerAppCombinedStrategy,
]

@Module({
  imports: [ServicesModule],
  controllers: sdkModuleControllers,
  providers: sdkModuleProviders,
})
export class SdkModule {}
