import { PassportStrategy } from '@nestjs/passport'
import { Strategy } from 'passport-local'
import { Injectable, UnauthorizedException } from '@nestjs/common'
import { CustomerAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class CustomerLocalStrategy extends PassportStrategy(Strategy, 'customerLocal') {
  constructor(private authService: CustomerAuthService) {
    super({
      usernameField: 'email',
    })
  }

  async validate(email: string, password: string): Promise<any> {
    const user = this.authService.validateUserReturnTokens('email', email, password)
    if (!user) {
      throw new UnauthorizedException()
    }
    return user
  }
}
