import {
  Body,
  Controller,
  Get,
  GoneException,
  Logger,
  Post,
  Query,
  Req,
  UseGuards,
  Version,
  VERSION_NEUTRAL,
} from '@nestjs/common'
import { OrderQuery, OrderTx } from '@flowda-projects/v1-flowda-services'
import { CustomerJwtAuthGuard } from '../customer/customerJwtAuth.guard'
import { AppJwtAuthGuard } from '../app/appJwtAuth.guard'
import {
  SdkCreateOrderDto,
  SdkCreateOrderInJSAPIDto,
  SdkCreateQuickOrderDto,
} from '@flowda-projects/flowda-shared-types'

@Controller('sdk/order')
export class OrderController {
  private readonly logger = new Logger('CustomerController')

  constructor(private orderQuery: OrderQuery, private orderTx: OrderTx) {}

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Post()
  async create(@Req() req, @Body() dto: SdkCreateOrderDto) {
    const user = req.user
    this.logger.log(`creating order controller ${dto.productId} from user: ${JSON.stringify(user)}`)
    return this.orderTx.create(user, dto)
  }

  @Version('4')
  @Post()
  async createV4(@Req() req, @Body() dto: SdkCreateOrderDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Post('createJSAPI')
  async createJSAPI(@Req() req, @Body() dto: SdkCreateOrderInJSAPIDto) {
    const user = req.user
    return this.orderTx.createJSAPI(user, dto)
  }

  @Version('4')
  @Post('createJSAPI')
  async createJSAPIV4(@Req() req, @Body() dto: SdkCreateOrderInJSAPIDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Post('quick')
  async quick(@Req() req, @Body() dto: SdkCreateQuickOrderDto) {
    const appId = req.user.id
    return this.orderTx.createQuick(appId, dto)
  }

  @Version('4')
  @Post('quick')
  async quickV4(@Req() req, @Body() dto: SdkCreateQuickOrderDto) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Get()
  query(@Query('orderId') orderId: string) {
    return this.orderQuery.query(orderId)
  }

  @Version('4')
  @Get()
  queryV4(@Query('orderId') orderId: string) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Get('quick/queryPay')
  queryPayQuick(
    @Req() req,
    @Query('anonymousCustomerToken') anonymousCustomerToken: string,
    @Query('orderId') orderId: string,
  ) {
    return this.orderTx.queryPayQuick(anonymousCustomerToken, orderId)
  }

  @Version('4')
  @Get('quick/queryPay')
  queryPayQuickV4(
    @Req() req,
    @Query('anonymousCustomerToken') anonymousCustomerToken: string,
    @Query('orderId') orderId: string,
  ) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(CustomerJwtAuthGuard)
  @Get('queryPay')
  queryPay(@Req() req, @Query('orderId') orderId: string) {
    return this.orderTx.queryPay(req.user.id, orderId)
  }

  @Version('4')
  @Get('queryPay')
  queryPayV4(@Req() req, @Query('orderId') orderId: string) {
    throw new GoneException()
  }
}
