import {
  Body,
  Controller,
  Get,
  GoneException,
  Post,
  Query,
  Req,
  UseGuards,
  Version,
  VERSION_NEUTRAL,
} from '@nestjs/common'
import { ProductQuery, ProductTx } from '@flowda-projects/v1-flowda-services'
import { AppJwtAuthGuard } from '../app/appJwtAuth.guard'
import { SdkProductCreateManyItemDto } from '@flowda-projects/flowda-shared-types'

@Controller('sdk/product')
export class ProductController {
  constructor(private query: ProductQuery, private tx: ProductTx) {}

  /**
   * todo: 删除，放到后台
   */
  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Post('createMany')
  async createMany(@Req() req, @Body() dto: SdkProductCreateManyItemDto[]) {
    const user = req.user
    const products = await this.tx.createManyProducts(user.id, dto)
    return {
      products,
    }
  }

  // todo: 仅集成测试暴露给 postman
  @Version('4')
  @Post('createMany')
  async createManyV4(@Req() req, @Body() dto: SdkProductCreateManyItemDto[]) {
    throw new GoneException()
  }

  @Version([VERSION_NEUTRAL, '1'])
  @UseGuards(AppJwtAuthGuard)
  @Get('findAll')
  async queryAll(@Req() req) {
    const user = req.user
    return this.query.findAll(user.id)
  }

  @Version('4')
  @Get('findAll')
  async queryAllV4(@Req() req) {
    throw new GoneException()
  }

  /**
   * 不用进行 appId+appToken 鉴权，直接根据 appId（在数据库里是 name 字段） 获取产品列表
   */
  @Version(VERSION_NEUTRAL)
  @Get('findAllByAppId')
  async queryAllByAppId(@Query('appId') name: string) {
    return this.query.findAllByAppName(name)
  }
}
