import { PassportStrategy } from '@nestjs/passport'
import { Strategy } from 'passport-custom'
import { Injectable, UnauthorizedException } from '@nestjs/common'
import { CustomerAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class FwhLoginSimpleStrategy extends PassportStrategy(Strategy, 'fwhLoginSimple') {
  constructor(private customerAuth: CustomerAuthService) {
    super()
  }

  /**
   * 简单校验下 state
   * @param request
   */
  async validate(request: Request): Promise<any> {
    const body: any = request.body
    const appId = (request as any).user?.id
    const ret = await this.customerAuth.validateState(body.state)
    if (!ret) {
      throw new UnauthorizedException()
    }
    return {
      valid: ret,
      appId,
    }
  }
}
