import { IResourceSchema } from '@flowda-projects/flowda-shared-types'

export function getSchemaByDisplayName(schemaCache: Record<string, IResourceSchema>, displayName: string) {
  const k = Object.keys(schemaCache).find(k => {
    return schemaCache[k].display_name === displayName
  })
  if (k) {
    return schemaCache[k]
  } else {
    return null
  }
}
