import { inject, injectable } from 'inversify'
import { IConfigService } from '../../infra'
import { WechatOAuth } from '../../legacy-libs'
import { TWxGetAccessTokenRes, TWxGetUser } from '@flowda-projects/flowda-shared-types'

@injectable()
export class WxLoginService {
  client: any

  constructor(
    @inject(IConfigService) private config: IConfigService,
    @inject('Factory<WechatOAuth>') private wechatOAuthFactory: () => typeof WechatOAuth,
  ) {}

  getAuthorizeURLForWebsite(redirectUrl: string) {
    const url = this.wechatOAuthFactory().getAuthorizeURLForWebsite(redirectUrl)
    return url
  }

  getAccessToken(code: string): Promise<{ data: TWxGetAccessTokenRes }> {
    return new Promise((resolve, reject) => {
      this.wechatOAuthFactory().getAccessToken(code, function (err: any, result: any) {
        if (err) {
          reject(err)
        } else {
          resolve(result)
        }
      })
    })
  }

  getUser(openid: string, access_token: string): Promise<TWxGetUser> {
    return new Promise((resolve, reject) => {
      this.wechatOAuthFactory()._getUser(
        {
          openid,
        },
        access_token,
        function (err: any, result: any) {
          if (err) {
            reject(err)
          } else {
            resolve(result)
          }
        },
      )
    })
  }
}
