import { PassportStrategy } from '@nestjs/passport'
import { Strategy } from 'passport-custom'
import { Injectable, UnauthorizedException, Logger } from '@nestjs/common'
import { CustomerAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class CustomerAppCombinedStrategy extends PassportStrategy(Strategy, 'customerAppCombined') {
  private readonly logger = new Logger('CustomerAppCombinedStrategy')
  constructor(private customerAuth: CustomerAuthService) {
    super()
  }

  async validate(request: Request): Promise<any> {
    this.logger.log('Customer App Combined Strategy guard: ', (request.body as any)?.email, (request as any).user)
    const body: any = request.body
    const appId = (request as any).user.id
    const user = await this.customerAuth.validateUserReturnTokens('email', appId, body?.email, body?.password)
    if (!user) {
      throw new UnauthorizedException()
    }
    return user
  }
}
