import { PassportStrategy } from '@nestjs/passport'
import { ExtractJwt, Strategy } from 'passport-jwt'
import { Injectable } from '@nestjs/common'
import { CustomerAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class CustomerJwtStrategy extends PassportStrategy(Strategy, 'customerJwt') {
  constructor(private service: CustomerAuthService) {
    super({
      jwtFromRequest: ExtractJwt.fromAuthHeaderAsBearerToken(),
      ignoreExpiration: false,
      secretOrKey: service.getAccessTokenSecret(),
    })
  }

  async validate(payload: any) {
    return this.service.getUser(payload.uid)
  }
}
