import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol, SdkProductCreateManyItemDto } from '@flowda-projects/flowda-shared-types'
import * as db from '@prisma/client-v1-flowda'
import { ProductService } from './product.service'

@injectable()
export class ProductTx {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(ProductService) private productService: ProductService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProductTx.name)
  }

  async createManyProducts(appId: string, list: SdkProductCreateManyItemDto[]): Promise<db.Product[]> {
    return this.prisma.$transaction(async tx => this.productService.createManyProducts(appId, list, { tx }))
  }
}
